import { createStore } from 'vuex'
import Web3 from 'web3';
const chainId = "970"
const mainAbi = [{"inputs":[{"internalType":"uint256","name":"","type":"uint256"}],"name":"allInscription","outputs":[{"internalType":"string","name":"tick","type":"string"},{"internalType":"uint256","name":"amt","type":"uint256"},{"internalType":"uint256","name":"limit","type":"uint256"},{"internalType":"uint256","name":"mintAmt","type":"uint256"},{"internalType":"address","name":"creator","type":"address"}],"stateMutability":"view","type":"function"},{"inputs":[],"name":"allInscriptionSize","outputs":[{"internalType":"uint256","name":"","type":"uint256"}],"stateMutability":"view","type":"function"},{"inputs":[{"internalType":"string","name":"tick","type":"string"},{"internalType":"address","name":"account","type":"address"}],"name":"balanceOf","outputs":[{"internalType":"uint256","name":"","type":"uint256"}],"stateMutability":"view","type":"function"},{"inputs":[{"internalType":"string","name":"tick","type":"string"}],"name":"checkTick","outputs":[{"internalType":"bool","name":"","type":"bool"}],"stateMutability":"view","type":"function"},{"inputs":[{"internalType":"string","name":"","type":"string"}],"name":"inscriptions","outputs":[{"internalType":"string","name":"tick","type":"string"},{"internalType":"uint256","name":"amt","type":"uint256"},{"internalType":"uint256","name":"limit","type":"uint256"},{"internalType":"uint256","name":"mintAmt","type":"uint256"},{"internalType":"address","name":"creator","type":"address"}],"stateMutability":"view","type":"function"},{"inputs":[{"internalType":"string","name":"","type":"string"}],"name":"minted","outputs":[{"internalType":"bool","name":"","type":"bool"}],"stateMutability":"view","type":"function"},{"inputs":[{"internalType":"string","name":"","type":"string"}],"name":"tickToken","outputs":[{"internalType":"address","name":"","type":"address"}],"stateMutability":"view","type":"function"},{"inputs":[{"internalType":"string","name":"","type":"string"}],"name":"tickUsed","outputs":[{"internalType":"bool","name":"","type":"bool"}],"stateMutability":"view","type":"function"},{"inputs":[{"internalType":"string","name":"","type":"string"}],"name":"totalSupply","outputs":[{"internalType":"uint256","name":"","type":"uint256"}],"stateMutability":"view","type":"function"}];
const mainContract = "0x05F7363A072f9ac830398133dfB228ABaeD4cA58";
const network = {chainId:'0x3ca',chainName:'Olympus',nativeCurrency:{name:'Olympus',symbol:'OORT',decimals:18},rpcUrls:['https://mainnet-rpc.oortech.com'],blockExplorerUrls:['https://mainnet-scan.oortech.com']}

export default createStore({
  state: {
    display_wallet_address:null,
    wallet_address:null,
    web3:null,
    contract:null,
    toAddress:mainContract
  },
  getters: {},
  mutations: {
    updateAddress(state, payload) {
      let addr = payload.address;
      if (addr) {
         let str_a = addr.substr(0, 5);
         let str_b = addr.substr(addr.length - 5);
         addr = str_a + "..." + str_b;
      }
      state.wallet_address = payload.address;
      state.display_wallet_address = addr;
   },
   updateWeb3(state, val) {
      state.web3 = val.web3;
      state.contract = new state.web3.eth.Contract(mainAbi,mainContract);
   }
  },
  actions: {
    async connectWallet({commit}) {
      try{
        if (window.ethereum) {
           let v = this;
           window.ethereum.on('accountsChanged', function (accounts) {
               if(accounts!=v.state.wallet_address){
                  location.reload()
               }
           });
           window.ethereum.on('networkChanged', async function (accounts) {
               if(accounts!=chainId){
                  await ethereum.request({method:'wallet_addEthereumChain',params:[network]}).catch(error=>{});
               }else{
                  await window.ethereum.enable();
                  commit('updateWeb3',{web3:new Web3(window.ethereum)});
                  let accounts = await window.ethereum.request({method:'eth_requestAccounts'});
                  commit('updateAddress',{address:accounts[0]});
               }
           });
           let cid = await new Web3(window.ethereum).eth.net.getId()+"";
           if(cid!=chainId){
               await ethereum.request({method:'wallet_addEthereumChain',params:[network]}).catch(error=>{});
           }else{
               await window.ethereum.enable();
               commit('updateWeb3',{web3:new Web3(window.ethereum)});
               let accounts = await window.ethereum.request({method:'eth_requestAccounts'});
               commit('updateAddress',{address:accounts[0]});
           }
        }
     }catch(e){
        window.notification["error"]({message:"Error",description:"Connect wallet error"});
     }
    }
  },
  modules: {

  }
})
