import { createRouter, createWebHistory } from 'vue-router'

const routes = [
  {
    path: '/',
    redirect: { name: 'Tokens' }
  },
  {
    path: '/tokens',
    name: 'Tokens',
    component: () => import('../views/Tokens/Tokens.vue'),
  },
  {
    path: '/info',
    name: 'Info',
    component: () => import('../views/Tokens/InfoView.vue')
  },
  {
    path: '/search',
    name: 'Search',
    component: () => import('../views/Tokens/SearchView.vue')
  },
  {
    path: '/marketplace',
    name: 'Marketplace',
    component: () => import('../views/Market/Marketplace.vue')
  },
  {
    path: '/owner',
    name: 'Owner',
    component: () => import('../views/Owner.vue')
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
